.orange {
  color: orange;
}

.profile {
  width: 300px;
  margin: 0 auto;
  text-align:center;
  padding: 1rem;
  background-color: #fff;
  border-radius: 20px;
  margin-bottom: 2rem;
  box-shadow: 7px 5px 23px -9px rgba(0,0,0,0.75);
  -webkit-box-shadow: 7px 5px 23px -9px rgba(0,0,0,0.75);
  -moz-box-shadow: 7px 5px 23px -9px rgba(0,0,0,0.75);
}

h1 {
  margin-top: 20px;
  margin-bottom: 20px;
}
p{
  margin-bottom: 20px;
}

.photo {
  width: 200px;
  height: 200px;
  border-radius : 100%;
}

.avatar {
  position: relative;
  width: 200px;
  height: 200px;
  margin: 2rem auto;
}

.new_tag {
  position: absolute;
  left: 80%;
  top: 10%; 
  font-weight: bold;
  font-size: 15px;
  background-color: lightcoral;
  border-radius: 0.5rem;
  padding: 0.2rem 0.5rem;
  color: #fff;
}
.container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.counter {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2rem;
  padding: 1rem;
  border-radius: 1rem;
  border: 2px solid rgb(215, 127, 12);
  width: 50vw;
}

.counter:hover {
  background-color: rgb(246,243,197);
}
.number {
  font-size: 7rem;
}
.button{
  font-size: 2rem;
  padding: 0.2rem 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
  background: rgb(215,127,12);
  background: linear-gradient(
    90deg,
    rgba(215,127,12,1) 0%,
    rgba(253,220,29,1)100%
  );
  margin-bottom: 10px;
}

.banner {
  margin-top: 50px;
  font-size: 30px;
  font-weight: bold;
  background-color: rgb(247, 232, 21);
  width: 65vw;
  text-align: center;
}
.inputBox {
  display: flex;
  align-items: baseline;
  justify-content: center;
}
.total {
  font-size: 1rem;
  margin-left: -1.5rem;
}
#checkbox {
  width: 16px;
  height: 16px;
  margin: 25px 0;
  cursor: pointer;
}
label {
  font-size: 20px;
  margin-left: 10px;
  cursor: pointer;
}
.ul {
  display: flex;
  list-style:none;
  justify-content: center;
}
.productList {
  background-color: #eee;
  width: 300px;
  text-align: center;
  padding-top: 20px;
}
.btn-box {
  margin: 0 auto;
  width: 100px;
  margin-top: 50px;
}
.btn {
  display: flex;
  width: 100px;
  height: 40px;
  border-radius: 5px;
  border: 1px solid gray;
  background-color: #fff;
  line-height: 40px;
  font-size: 20px;
  justify-content: center;
  cursor: pointer;
}
.btn:hover {
  background-color: gray;
  color: #fff;
  border: none;
}